





































































































































@import '~styles/base';

.level51-ajaxMultiSelectField {
  .level51-ajaxMultiSelectField-items {
    margin-top: @space-3;

    table {
      border-collapse: collapse;
      table-layout: fixed;
      width: 100%;

      td, th {
        padding: @space-1 @space-2;
        border: 1px solid @color-light-grey;
      }

      .level51-ajaxMultiSelectField-actions {
        width: 60px;
        text-align: center;
      }
    }
  }
}
